/* fonts
@include font-face("MiniSSF Bold", "../fonts/MINI_Sans_Serif_Bold/MINI_Sans_Serif_Bold"); 
*/
@import url(https://fonts.googleapis.com/css?family=Seaweed+Script);
[class*="border"] {
  border: 1px solid #000 !important; }

.border-5 {
  border: 5px solid #000 !important; }

.border-top {
  border-width: 0 !important;
  border-top: 1px solid #000 !important; }

.border-bottom {
  border-width: 0 !important;
  border-bottom: 1px solid #000 !important; }

.border-top-5 {
  border-width: 0 !important;
  border-top-width: 5px !important; }

.border-bottom-5 {
  border-width: 0 !important;
  border-bottom-width: 5px !important; }

.border-bottom-10 {
  border-width: 0 !important;
  border-bottom-width: 10px !important; }

.h-rotate-180 {
  transform: rotate(180deg); }

.no-padding {
  padding: 0;
  margin: 0; }

html {
  font-size: 62.5%; }

body {
  color: #000;
  margin: 0;
  padding: 0;
  font-family: "Source Code Pro", Courier, mono; }

.main-content {
  max-width: 1280px;
  margin: auto;
  overflow: hidden; }

ul, ol, li {
  list-style: none;
  margin: 0;
  padding: 0; }

.no-padding {
  margin: 0;
  padding: 0; }

.h1 {
  line-height: 2.6rem;
  font-size: 2.6rem;
  text-align: left;
  text-transform: uppercase;
  margin-top: 2rem;
  margin-bottom: 2rem; }

p {
  font-family: sans-serif;
  font-weight: bold; }

@media screen and (max-width: 1199px) {
  .display-desktop {
    display: none !important; } }

@media screen and (min-width: 1200px) {
  .display-mobile {
    display: none !important; } }

.nav-menu {
  display: block; }

.gs-header {
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid #37135a;
  padding: 1.5rem 0 1.5rem 0;
  margin-bottom: 6rem; }
  .gs-header_logo {
    display: inline-block; }
    .gs-header_logo .gs-logo {
      width: 10rem;
      height: 6rem; }
    .gs-header_logo .gs-slogan {
      font-size: 1.6rem;
      color: #37135a;
      font-weight: bold; }
  .gs-header_menu {
    text-align: center;
    margin-top: 1rem; }
    .gs-header_menu .gs-menu {
      display: inline-block;
      margin: 0;
      padding: 0;
      list-style: none;
      transform: skew(-25deg); }
      .gs-header_menu .gs-menu_item {
        font-size: 1.8rem;
        padding: 0.4rem;
        background: #F0F8FF;
        float: left;
        border-right: 1px solid #eee;
        box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
        text-transform: uppercase;
        color: #37135a;
        font-weight: bolder;
        transition: all 0.3s linear; }
        .gs-header_menu .gs-menu_item:last-child {
          border-right: none;
          border-radius: 0 7px 7px 0; }
        .gs-header_menu .gs-menu_item:hover {
          background: #eee;
          color: #37135a; }
      .gs-header_menu .gs-menu_link {
        color: #37135a;
        display: block;
        padding: 0em 1.5em;
        color: inherit;
        text-decoration: none;
        transform: skew(25deg); }

.footer {
  display: block; }

.gs-article {
  font-family: Merriweather, Georgia, "Times New Roman", serif; }
  .gs-article_title {
    color: rgba(0, 133, 172, 0.3);
    font-size: 2.4rem; }
  .gs-article_img {
    width: 64rem;
    height: 45rem; }

.gs-article h1 {
  text-align: justify;
  font-size: 3rem; }

.gs-article h2 {
  text-align: justify;
  font-size: 2.8rem; }

.gs-menu_item {
  font-size: 1.4rem;
  padding: 0.4rem; }

.gs-btn {
  width: 26rem;
  height: 6rem;
  font-size: xx-large;
  /* margin-right: 8rem; */ }

.gs-canvas {
  background-color: aliceblue;
  min-height: 100vh; }

.gs-errorMsg_item {
  color: red;
  font-size: 1.5rem;
  font-family: sans-serif;
  margin-left: 10rem; }

.gs-StatusMsg_item {
  color: green;
  font-size: 1.5rem;
  font-family: "Source Sans Pro", Helvetica, Verdana, sans-serif;
  margin-left: 10rem; }

.gs-tasks-block {
  width: fit-content;
  margin-left: 34rem;
  margin-right: 27rem; }
  .gs-tasks-block .gs-menu {
    display: inline-block;
    margin: 0;
    list-style: none;
    margin-bottom: 0;
    background: linear-gradient(to right top, white, lightslategray);
    width: 100%;
    transform: skew(0deg);
    border-radius: 1rem 1rem 0rem 0rem; }
    .gs-tasks-block .gs-menu_item {
      font-size: 1.5rem;
      padding: 0.9rem;
      float: left;
      border-right: 1px solid #eee;
      box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
      color: #37135a;
      font-weight: bolder;
      transition: all 0.3s linear; }
      .gs-tasks-block .gs-menu_item:first-child {
        border-radius: 7px 0 0 7px; }
      .gs-tasks-block .gs-menu_item:last-child {
        border-right: none;
        border-radius: 0 7px 7px 0; }
      .gs-tasks-block .gs-menu_item:hover {
        background: #eee;
        color: #37135a; }
    .gs-tasks-block .gs-menu_link {
      color: #37135a;
      display: block;
      padding: 0em 1.5em;
      color: inherit;
      text-decoration: none;
      transform: skew(25deg); }

.gs-Deleteform {
  place-items: center;
  justify-items: stretch; }
  .gs-Deleteform_fieldset {
    border: 0.2rem solid #c1c1c1;
    padding: 3rem;
    margin: 0 auto 0 auto;
    width: 44rem;
    height: 24rem;
    max-width: 100rem;
    background: linear-gradient(to right bottom, white, lightslategrey); }
  .gs-Deleteform .Delete {
    background: royalblue;
    border: royalblue;
    border-radius: 35px;
    float: right;
    width: 12rem;
    height: 3.5rem;
    font-size: large;
    font-weight: 600;
    margin-top: 4rem;
    color: blanchedalmond;
    text-shadow: 0 0 0.5em violet, 0 0 0.5em cyan, 0 0 0.5em #fff; }

.gs-form {
  min-height: 100vh;
  place-items: center;
  justify-items: stretch; }
  .gs-form_fieldset {
    border: 0.2rem solid #c1c1c1;
    padding: 3rem;
    margin: 2rem auto 0 auto;
    width: 90rem;
    max-width: 100rem;
    background: linear-gradient(to right bottom, white, lightslategrey); }
  .gs-form_group .label {
    font-size: 1.4rem;
    text-align: right;
    display: block;
    padding: 0.5em 1.5em 0.5em 0;
    font-weight: bold;
    font-family: "Source Sans Pro", Helvetica, Verdana, sans-serif; }
  .gs-form_group .form-control {
    align-items: left;
    border: 1px solid #9c9c9c;
    width: 100%;
    padding: 0.4rem;
    margin-bottom: 0.5rem;
    font-size: medium;
    color: black;
    font-weight: bold; }
  .gs-form .submit {
    padding: 0.4rem;
    font-size: 2rem;
    width: 18rem;
    height: 4.4rem;
    margin: 2.4rem 2rem 0 auto; }

.gs-Loginform {
  place-items: center;
  justify-items: stretch; }
  .gs-Loginform_fieldset {
    border: 0.2rem solid #c1c1c1;
    padding: 3rem;
    margin: 0 auto 0 auto;
    width: 44rem;
    height: 30rem;
    max-width: 100rem;
    background: linear-gradient(to right bottom, white, lightslategrey); }
  .gs-Loginform .Login {
    background: royalblue;
    border: royalblue;
    border-radius: 35px;
    float: right;
    width: 12rem;
    height: 3.5rem;
    font-size: large;
    font-weight: 600;
    margin-top: 4rem;
    color: blanchedalmond;
    text-shadow: 0 0 0.5em violet, 0 0 0.5em cyan, 0 0 0.5em #fff; }

.gs-Registerform {
  place-items: center;
  justify-items: stretch; }
  .gs-Registerform_fieldset {
    border: 0.2rem solid #c1c1c1;
    padding: 3rem;
    margin: 0 auto 0 auto;
    width: 46rem;
    max-width: 100rem;
    background: linear-gradient(to right bottom, white, lightslategrey); }
  .gs-Registerform .Login {
    background: royalblue;
    border: royalblue;
    border-radius: 35px;
    float: right;
    width: 12rem;
    height: 3.5rem;
    font-size: large;
    font-weight: 600;
    margin-top: 4rem;
    color: blanchedalmond;
    text-shadow: 0 0 0.5em violet, 0 0 0.5em cyan, 0 0 0.5em #fff; }
  .gs-Registerform .summary {
    font-size: 1.3rem;
    font-weight: bold;
    font-family: sans-serif; }

.gs-export {
  float: right;
  color: #fff;
  background-color: darkcyan;
  -webkit-border-radius: 2rem;
  -moz-border-radius: 2rem;
  border-radius: 2rem;
  width: 10rem;
  height: 3.5rem;
  font-size: 2rem;
  text-align: center;
  padding-top: 0.3rem;
  margin-left: 1.2rem;
  margin-top: 3rem; }

.gs-table {
  margin-top: 3rem; }
  .gs-table .action-view {
    width: 13rem; }
  .gs-table .action-edit {
    display: none; }
  .gs-table .action-delete {
    display: none; }

.btn-GoBack {
  float: right;
  width: 15rem;
  height: 4rem;
  font-size: 2.5rem; }

.gs-table th {
  font-weight: bold;
  font-family: "Source Sans Pro", Helvetica, Verdana, sans-serif;
  font-size: 2rem;
  color: midnightblue; }

.gs-table tr {
  padding: 0.75rem;
  vertical-align: top;
  border-top: 1px solid #dee2e6;
  font-size: 1.8rem;
  font-family: sans-serif; }

@media only screen and (max-width: 700px) {
  body {
