    width: 45.5rem; }
    .gs-tasks-block .gs-menu_item {
      width: 20rem; }
  .gs-btn {
    margin-top: 1.5rem;
    margin-left: 5rem; }
  .gs-form_fieldset {
    width: 30rem; }
  .gs-Deleteform_fieldset {
