@media only screen and (min-width: 700px) and (max-width: 1200px) {
  body {
    margin: 0;
    padding: 0;
    width: 100%; }
  .gs-canvas {
    width: 100%;
    margin: 0;
    padding: 0; }
  .gs-article_img {
    width: 34rem; }
  .gs-menu_item {
    width: 8rem; }
  .gs-slogan {
    display: block; }
  .gs-tasks-block {
    margin-left: 4rem;
    width: 45.5rem; }
    .gs-tasks-block .gs-menu_item {
      width: 20rem; }
  .gs-btn {
    width: 17rem;
    font-size: 2.5rem; }
  .gs-form_fieldset {
    width: 60rem; } }

.gs-title {
  text-align: center; }

.gs-span {
  font-family: "Seaweed Script";
  color: royalblue;
  text-align: center;
  font-size: 4.5rem;
  position: relative;
  padding: 0 0.3em; }
